import React, { Component } from 'react';
import { Route, Switch } from 'react-router-dom';
import { connect } from 'react-redux';

import Header from '../../containers/header';
import BillingHome from '../billingHome';
import Downloads from '../../containers/downloads';

class Billing extends Component {
    render() {
        return (
            <div className="billing_container">
                <Header className="bc_header"/>
                <Switch>
                    {
                        this.props.config.downloads &&
                        <Route path={this.props.match.url+"downloads"} render={(props) => {  return <Downloads {...props} pathUrl={this.props.match.path}/>}}/>
                    }
                    <Route path={this.props.match.url} render={(props) => {  return <BillingHome {...props} />}} />

                </Switch>
            </div> 
        );
    }
}

const mapStateToProps = state => ({
    config: state.config
});
  
const mapDispatchToProps = dispatch =>({
    
});

export default connect(mapStateToProps, mapDispatchToProps)(Billing);
